<template>
    <div v-loading="loading">
        <div class="container">
            <el-row  class="toolbar">
                <el-input
                    placeholder="输入关键字进行过滤"
                    v-model="filterText">
                </el-input>
                <el-tree
                    ref="domTree"
                    :data="data5"
                    node-key="id"
                    default-expand-all
                    :filter-node-method="handleFilterNode"
                    @node-contextmenu="contextmenu"
                    :expand-on-click-node="false">
                    <span class="custom-tree-node" slot-scope="{ node, data }">
                        <span>{{ data.mc_name }}</span>
                        <span>
                            <el-button
                                type="text"
                                size="mini"
                                @click="handleTreeAdd(node, data)">
                                添加
                            </el-button>
                            <!-- <el-button
                                type="text"
                                size="mini"
                                :ref="`domTreeAdd${data.id}`"
                                @click="append(node, data)">
                                添12加
                            </el-button> -->
                            <el-button
                                type="text"
                                size="mini"
                                @click="handleTreeEdit(node, data)">
                                编辑
                            </el-button>
                            <el-button
                                type="text"
                                size="mini"
                                @click="handleTreeDel(node, data)">
                                删除
                            </el-button>
                        </span>
                    </span>
                </el-tree>
            </el-row>
        </div>
        
        <!-- 添加公司 -->
        <el-dialog :title="treeAddTitle" :visible.sync="dialogTree" width="50%">
            <el-form ref="formTree" :model="formTree" label-width="80px" :rules="rules">
                <el-form-item label="公司名称" prop="mc_name">
                    <el-input  v-model="formTree.mc_name"></el-input>
                </el-form-item>
                <el-form-item label="负责人">
                    <el-input  v-model="formTree.mc_user"></el-input>
                </el-form-item>
                <el-form-item label="移动电话">
                    <el-input  v-model="formTree.mc_mobile"></el-input>
                </el-form-item>
                <el-form-item label="公司地址">
                    <el-input  v-model="formTree.mc_address"></el-input>
                </el-form-item>
                <el-form-item label="上级公司">
                    <el-select v-model="formTree.mc_parent_id" placeholder="请选择" class="mr10">
                        <el-option
                            v-for="item in parentOptions"
                            :key="item.id"
                            :label="item.mc_name"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="营业执照" prop="url">
                    <el-upload
                        class="avatar-uploader"
                        :action="IMG_UPLOAD_URL"
                        :data="{type:5}"
                        :show-file-list="false"
                        :on-success="handleUploadSuccess"
                        :before-upload="handleBeforeUpload">
                        <img v-if="formTree.url" :src="formTree.url" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="统一社会信用代码" prop="mc_credit_code">
                    <el-input  v-model="formTree.mc_credit_code"></el-input>
                </el-form-item>
                
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogTree = false">取 消</el-button>
                <el-button type="primary" @click="handleSubmitTree">确 定</el-button>
            </span>
        </el-dialog>
    
    </div>
</template>

<script>
    import * as API from '@/api/enterpriseManagement'
    import { requestUrl } from '@/api/config'
    export default {
        data() {
            const data = [];
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                tableData: [],
                filterText: '',
                parentOptions: [],
                treeAddTitle: '',
                loading: true,
                dialogTree: false,
                uploadVisible: false,
                data5: JSON.parse(JSON.stringify(data)),
                treeNewChild: null,
                treeActiveData: null,
                treeActiveId: null,
                formTree: {
                    mc_address: "",
                    url: "",
                    mc_credit_code: "",
                    mc_mobile: "",
                    mc_name: "",
                    mc_parent_id: "",
                    mc_user: ""
                },
                rules: {
                    mc_name:[
                        {required: true, message:'请输入公司名称', trigger:'blur'}
                    ],
                    url:[
                        {required: true, message:'请输入公司名称', trigger:'blur'}
                    ],
                    mc_credit_code:[
                        {required: true, message:'请输入公司名称', trigger:'blur'}
                    ]
                }
                
            }
        },
        mounted() {
            this.getTreeList();
        },
        computed: {
            
        },
        watch: {
            filterText(val) {
                this.$refs.domTree.filter(val);
            }
        },
        methods: {
            getTreeList(){
                API.organizationList({}).then(res => {
                    this.data5 = JSON.parse(JSON.stringify(res.data))
                    this.loading = false
                })
            },
            getTreeListUl(){
                API.organizationListUl({}).then(res => {
                    this.parentOptions = res.data
                })
            },
            append(node,data) {
                const newChild = { id: node.id++, mc_name: 'testtest', children: [] };
                if (!data.children) {
                    this.$set(data, 'children', []);
                }
                data.children.push(newChild);
            },
            handleTreeAdd(node, data){
                
                this.dialogTree = true
                this.treeActiveId = data.id
                this.treeAddTitle = '添加公司'
                this.getTreeListUl()
                Object.assign(this.formTree, {
                    mc_address: "",
                    url: "",
                    mc_credit_code: "",
                    mc_mobile: "",
                    mc_name: "",
                    mc_parent_id: "",
                    mc_user: ""
                })
                this.formTree.mc_parent_id = node.data.id
                // node.parent.id == 0 ? this.formTree.mc_parent_id = node.data.id : this.formTree.mc_parent_id = node.parent.data.id
                /*console.log(node, data)
                const newChild = { id: node.id++, mc_name: 'testtest', children: [] };
                if (!data.children) {
                  this.$set(data, 'children', []);
                }
                this.treeNewChild = newChild
                this.treeActiveData = data*/
                // data.children.push(newChild);
                
            },
            handleSubmitTree(){
                // this.$refs.domTreeAdd.click()
                // this.$refs['domTreeAdd'+this.treeActiveId].handleClick()
                
                this.$refs.formTree.validate((valid)=>{
                    if(valid){
                        if(this.treeAddTitle == '添加公司'){
                            API.organizationAdd(this.formTree).then(res => {
                                this.dialogTree = false
                                this.getTreeList()
                            })
                        }else{
                            API.organizationEdit(this.formTree).then(res => {
                                this.dialogTree = false
                                this.getTreeList()
                            })
                        }
                    }else{
                        return false
                    }
                })
                
                
                
            },
            handleTreeEdit(node, data){
                this.dialogTree = true
                this.treeActiveId = data.id
                this.treeAddTitle = '编辑公司'
                this.getTreeListUl()
                Object.assign(this.formTree, data)
                this.formTree.mc_parent_id = node.parent.data.id
                
            },
            handleTreeDel(node, data){
                const parent = node.parent;
                const children = parent.data.children || parent.data;
                const index = children.findIndex(d => d.id === data.id);
                API.organizationDel({id: data.id}).then(res => {
                    if(res.code == 0){
                        children.splice(index, 1);
                    }else{
                        this.$message.error(res.msg)
                    }
                    
                })
            },
            handleUploadSuccess(res, file, fileList){
                this.formTree.url = res.data.url
                this.formTree.mc_credit_code = res.data.register_number
            },
            handleBeforeUpload(){

            },
            contextmenu(a,b,c,d){
                console.log(a,b,c)
            },
            handleFilterNode(value, data) {
                if (!value) return true;
                return data.mc_name.indexOf(value) !== -1;
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
    .custom-tree-node {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        padding-right: 8px;
    }
</style>
